


























/* only for app */
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#output {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.rune-generator {
  width: 800px;
}
.rune-selection {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around
}
select {
  width: 100px;
}
/* common */
.rune-columns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.rune-column {
  width: 400px;
  height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  border: 1px solid #ddd;
  background: #ebe9e9;
}
.expander {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.rune-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-top: 10px;
}
.rune-build {
  width: 125px;
  height: 125px;
  border-radius: 50%;
}
.rune-item {
  position: relative;
  display: inline-block;
}
.rune-item img{
  width: 65px;
  height: 65px;
  opacity: 0.3;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.rune-row-keystone .rune-item img {
  width: 100px;
  height: 100px;
}
.rune-item-active img{
  opacity: 1.0;
}
.rune-item .rune-tooltip {
  visibility: hidden;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px;
  border-radius: 8px;

  position: absolute;
  z-index: 1;
  bottom: 110%;
  left: 50%;
  margin-left: -60px;

  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.rune-item .rune-tooltip::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}
.rune-item:hover .rune-tooltip {
    visibility: visible;
    opacity: 1;
}
